/* eslint-disable */
import Vue from 'vue'
import Router from 'vue-router'
import Xzpx from '@/components/Xzpx'
import Mppx from '@/components/Mppx'
import Crpx from '@/components/Crpx'
import Sort from '@/components/Sort'
Vue.use(Router)

export default new Router({
    routes: [
        { path: '/', redirect: '/sort' },
        {
            path: '/xzpx',
            component: Xzpx
        }, {
            path: '/mppx',
            component: Mppx
        }, {
            path: '/crpx',
            component: Crpx
        }, {
            path: '/sort',
            component: Sort
        }
    ]

})


