<script>
  export default {
    data() {
      return {
         input1:'',
         input2:'',
         objects:[],
         ip1:[],
         ip2:[],
         len:0,
         myChart:'',
         s1:0,s2:0,s3:0,s4:0,s5:0,s6:0,
         wdx:0,min:0,max:1000,count:100,
         op1:false,op2:false,op3:false,op4:false,op5:false,op6:false,
         time1:'',
         time2:'',
         time3:'',
         time4:'',
         time5:'',
         time6:'',
         result1:'',
         result2:'',
         result3:'',
         result4:'',
         result5:'',
         result6:'',
         wdx1:[],wdx2:[],wdx3:[],
         wdx4:[],wdx5:[],wdx6:[],
         wdx0:[],
      }
    },
    methods: {
    drawline() {
            var echarts = require('echarts');  //上述引入不全
            // import { push } from 'echarts/lib/component/dataZoom/history'
            // let echarts = require('echarts/lib/echarts')
            // Introduction of histogram component
            // require('echarts/lib/chart/line')
            // // Introduce prompt box and title component
            // require('echarts/lib/component/tooltip')
            // require('echarts/lib/component/title')
            // require('echarts/lib/component/legend')
            var Chart = echarts.init(document.getElementById('main'))
            this.myChart=Chart;
            Chart.setOption({
                title: {
                    text: ''
                },
                tooltip: {},
                legend: {
                    data:['排序时间']
                },
                grid: {
                    left: '3%',
                    right: '4%',
                    bottom: '3%',
                    containLabel: true
                },
                xAxis: {
                    // type: 'category',
                    // boundaryGap: false,
                    data: ['冒泡排序','选择排序','插入排序','快速排序','堆排序','希尔排序']
                },
                yAxis: {
                    type: 'value',
                    name:'ms',
                    nameLocation:'end',
                },
                series: [{
                      name: '排序时间',
                      type: 'line',
                      data: this.objects,
                  }
              ]

            });
            console.log(this.objects);
     },
    update: function (e) {
      this.$forceUpdate()	// 刷新
    },
    xntest(){
        if(this.input1=='')
        {
          this.$message({
          message: '排序数据输入不能为空',
          type: 'error',
          offset:138,
          })
        }
        else{
        this.ip1=this.input1.split(" ");
        console.log(this.ip1);
        this.xzpx();this.mppx();this.crpx();
        this.kspx();this.dpx();this.xepx();
        this.objects.push(this.time1);
        this.objects.push(this.time2);
        this.objects.push(this.time3);
        this.objects.push(this.time4);
        this.objects.push(this.time5);
        this.objects.push(this.time6);
        console.log(this.objects);
        this.drawline();
        }
    },
    wdxtest(){
        if(this.input1=='')
        {
          this.$message({
          message: '排序数据输入不能为空',
          type: 'error',
          offset:119,
          })
        }
       if(this.input2=='')
       {
          this.$message({
          message: '关键字输入不能为空',
          type: 'error',
          offset:138,
          })
        }
        else
        {
          this.ip1=this.input1.split(" ");
          this.ip2=this.input2.split(" ");
          var len1=this.ip1.length;
          var len2=this.ip2.length;
          if(len1!=len2)
          {
            this.$message({
              message:'请输入相同的数据长度!',
              type:'error',
              offset:138,
            })
          }
          else{
            var lm=Array.from(this.ip1);
            var ln=new Array(len1).fill(0);
            var lp=new Array(len1).fill(1);//没重复过就是1
            for(var i=0;i<len1-1;i++)
            {
              for(var j=i+1;j<len1;j++)
              if(lm[i]==lm[j]&&lp[j]==1)
              {
                ln[i]=i+1;ln[j]=j+1;
                lp[i]=lp[j]=0;
              }
            }
            console.log(ln); 
            for(var i=0;i<len1;i++)
            {
              var object={
                data:Number(this.ip1[i]),
                key:this.ip2[i],
                rep:ln[i],
                flag:false,
              };
              this.objects.push(object);
            } 
            console.log(JSON.stringify(this.objects));
            this.mppx0();this.xzpx0();this.crpx0();
            this.kspx0();this.dpx0();this.xepx0();
            this.wdx=1; 
          }
        } 
    },
    clear(){
      this.s1=0;this.s2=0;this.s3=0;this.s4=0;this.s5=0;this.s6=0;
      this.wdx=0;
      this.input1='';
      this.input2='';
      this.ip1=[];this.ip2=[];this.wdx0=[],
      this.objects=[];
      this.myChart.clear();
    },
    randnumber(){
      var l=[];
      function randomNum(minNum,maxNum){ 
          switch(arguments.length){ 
              case 1: 
                  return parseInt(Math.random()*minNum+1,10); 
              break; 
              case 2: 
                  return parseInt(Math.random()*(maxNum-minNum+1)+minNum,10); 
              break; 
              default: 
                  return 0; 
              break; 
          } 
      };
      for(var i=0;i<this.count;i++)
      {
        // var dd=randomNum(this.min,this.max)
        // if(i=0 || i>0 && dd > l[i-1])
        // l.push[dd];
        l.push(randomNum(this.min,this.max));
        // else
        // i--;
      }
      console.log(l);
      this.input1=l.join(" ");
    },
    mppx0(){
        var len=this.ip1.length;
        var l1=JSON.parse(JSON.stringify(this.objects));
        var temp;
         for(var i=0;i<len-1;i++)
         for(var j=0;j<len-i-1;j++){
              if(l1[j].data>l1[j+1].data)
              {
                temp=l1[j];
                l1[j]=l1[j+1];
                l1[j+1]=temp;
                    }
         }
        // this.wdx1=l1; 
        for(var i=0;i<len-1;i++)
        {
          if(l1[i].data==l1[i+1].data && l1[i].rep>l1[i+1].rep)
          {
            this.result1=86;
            l1[i].flag=l1[i+1].flag=true;
          }
        }   
        this.wdx1=l1;
        console.log(JSON.stringify(l1));
    },
    xzpx0(){
        var len=this.ip1.length;
        var l2=JSON.parse(JSON.stringify(this.objects));
        var temp;
         for(var i=0;i<len-1;i++)
         for(var j=i+1;j<len;j++){
             if(l2[j].data<l2[i].data)
            {
              temp=l2[j];
              // console.log(j);
              l2[j]=l2[i];
              l2[i]=temp;
            }
         };
        //this.wdx2=l2; 
        for(var i=0;i<len-1;i++)
        {
          if(l2[i].data==l2[i+1].data && l2[i].rep>l2[i+1].rep)
          {
            this.result2=86;
            l2[i].flag=l2[i+1].flag=true;
          }
        }    
        this.wdx2=l2;
        console.log(JSON.stringify(l2));
    },
    crpx0(){
        var len=this.ip1.length;
        var l3=JSON.parse(JSON.stringify(this.objects));
        var preIndex, current;
        for (let i = 1; i < len; i++) {
          preIndex = i - 1;
          var temp=l3[i];
          current = l3[i].data;
          while (preIndex >= 0 && current < l3[preIndex].data) {
            l3[preIndex + 1] = l3[preIndex];
            preIndex--;
          }
                l3[preIndex + 1] =temp;
        };
        //this.wdx3=l3;  
        for(var i=0;i<len-1;i++)
        {
          if(l3[i].data==l3[i+1].data && l3[i].rep>l3[i+1].rep)
          {
            this.result3=86;
            l3[i].flag=l3[i+1].flag=true;
          }
          if(l3[i].data==l3[i+1].data)
          {
            if(i>=1 && l3[i-1].data!=l3[i].data)
            this.wdx0.push(l3[i]);
            this.wdx0.push(l3[i+1]);
            if(i<=len-3 && l3[i+1].data!=l3[i+2].data)
            {
              this.wdx0.push('186');
            }
          }
        } 
        console.log(this.wdx0);
        this.wdx3=l3;
        console.log(JSON.stringify(l3));
    },
    kspx0(){
        var len=this.ip1.length;
        var l4=JSON.parse(JSON.stringify(this.objects));
        function partition(r, low, high)
        {
          var w=r[low].data;
          var wh=r[low];
          while(low<high){
            while(low<high && r[high].data>=w)
            --high;
            r[low]=r[high];
            while(low<high && r[low].data<=w)
            ++low;
            r[high]=r[low];
          }
          console.log(low);
          r[low]=wh;
          return low;
        }
        function qsort(r,low,high) {
          if(low<high){
              var index=partition(r,low,high);
              qsort(r,low,index-1);
              qsort(r,index+1,high);
          }
        }
        qsort(l4,0,len-1);
        //this.wdx4=l4;   
        for(var i=0;i<len-1;i++)
        {
          if(l4[i].data==l4[i+1].data && l4[i].rep>l4[i+1].rep)
          {
            this.result4=86;
            l4[i].flag=l4[i+1].flag=true;
          }
        }  
        this.wdx4=l4;
        console.log(JSON.stringify(l4));
    },
    dpx0(){
      var len=this.ip1.length;
      var leng=len;
      var l5=JSON.parse(JSON.stringify(this.objects));
      function buildMaxHeap(arr) {   // 建立大顶堆
          len = arr.length;
          for (var i = Math.floor(len/2); i >= 0; i--) {
              heapify(arr, i);
          }
      }
      function heapify(arr, i) {     // 堆调整
          var left = 2* i + 1,
              right = 2 * i + 2,
              largest = i;

          if (left < len && arr[left].data > arr[largest].data) {
              largest = left;
          }
      
          if (right < len && arr[right].data > arr[largest].data) {
              largest = right;
          }
      
          if (largest != i) {
              swap(arr, i, largest);
              heapify(arr, largest);
          }
      }
      
      function swap(arr, i, j) {
          var temp = arr[i];
          arr[i] = arr[j];
          arr[j] = temp;
      }
      
      function heapSort(arr) {
          buildMaxHeap(arr);
          for (var i = arr.length - 1; i > 0; i--) {
              swap(arr, 0, i);
              len--;
              heapify(arr, 0);
          }
          return arr;
      }
      heapSort(l5);
     // this.wdx5=l5;
     var j=0;
        for(var i=0;i<leng-1;i++)
        {
          if(l5[i].data==l5[i+1].data && l5[i].rep>l5[i+1].rep)
          {
            this.result5=86;
            j++;
            l5[i].flag=l5[i+1].flag=true;
          }
        }
        console.log(j);
        this.wdx5=l5;
        console.log(JSON.stringify(l5));
    },
    xepx0(){
      var len=this.ip1.length;
      var l6=JSON.parse(JSON.stringify(this.objects));
      function shellSort(arr) {
      var len = arr.length;
      for (var gap = Math.floor(len / 2); gap > 0; gap = Math.floor(gap / 2)) {
          for (var i = gap; i < len; i++) {
              var j = i;
              var tool=arr[i];
              var current = arr[i].data;
              while (j - gap >= 0 && current < arr[j - gap].data) {
                  arr[j] = arr[j - gap];
                  j = j - gap;
              }
              arr[j] = tool;
          }
      }
      return arr;
      }
      shellSort(l6);
      //this.wdx6=l6;     
        for(var i=0;i<len-1;i++)
        {
          if(l6[i].data==l6[i+1].data && l6[i].rep>l6[i+1].rep)
          {
            this.result6=86;
            l6[i].flag=l6[i+1].flag=true;
          }
        }
        this.wdx6=l6;
      console.log(JSON.stringify(l6));
    },

   
    xzpx(){
        var len=this.ip1.length;
        var l2=Array.from(this.ip1);
        l2 = l2.map(Number);
        var s=performance.now();

        var temp=0;
         for(var i=0;i<len-1;i++)
         for(var j=i+1;j<len;j++){
             if(l2[j]<l2[i])
            {
              temp=l2[j];
              l2[j]=l2[i];
              l2[i]=temp;
            }
         }

        var e=performance.now();
        this.time2=e-s;
        console.log(e-s);
        console.log(l2);
        this.result2=l2.join(" ");
        this.s2=1;//状态
    },
    mppx(){
        var len=this.ip1.length;
        var l1=Array.from(this.ip1);
        l1 = l1.map(Number);
        var s=performance.now();

        var temp=0;
         for(var i=0;i<len-1;i++)
         for(var j=0;j<len-i-1;j++){
              if(l1[j]>l1[j+1])
              {
                temp=l1[j];
                l1[j]=l1[j+1];
                l1[j+1]=temp;
              }
         }
        
        var e=performance.now();
        this.time1=e-s;
        console.log(e-s);
        console.log(l1);
        this.result1=l1.join(" ");
        this.s1=1;//状态
    },
    crpx(){
        var len=this.ip1.length;
        var l3=Array.from(this.ip1);
        l3 = l3.map(Number);
        var s=performance.now();

        let preIndex, current;
        for (let i = 1; i < len; i++) {
          preIndex = i - 1;
          current = l3[i];
          while (preIndex >= 0 && current < l3[preIndex]) {
            l3[preIndex + 1] = l3[preIndex];
            preIndex--;
          }
          l3[preIndex + 1] = current;
        }

        var e=performance.now();
        this.time3=e-s;
        console.log(e-s);
        console.log(l3);
        this.result3=l3.join(" ");
        this.s3=1;//状态
    },
    kspx(){
        var len=this.ip1.length;
        var l4=Array.from(this.ip1);
        l4 = l4.map(Number);
        function partition(r, low, high)
        {
          var w=r[low];
          while(low<high){
            while(low<high && r[high]>=w)
            --high;
            r[low]=r[high];
            while(low<high && r[low]<=w)
            ++low;
            r[high]=r[low];
          }
          r[low]=w;
          return low;
        }
        function qsort(r,low,high) {
          if(low<high){
              var index=partition(r,low,high);
              qsort(r,low,index-1);
              qsort(r,index+1,high);
          }
        }
        var s=performance.now();

        qsort(l4,0,len-1);

        var e=performance.now();
        this.time4=e-s;
        console.log(e-s);
        console.log(l4);
        this.result4=l4.join(" ");
        this.s4=1;//状态
    },
    dpx(){
      var len=this.ip1.length;
      var l5=Array.from(this.ip1);
      l5 = l5.map(Number);
      function buildMaxHeap(arr) {   // 建立大顶堆
          len = arr.length;
          for (var i = Math.floor(len/2); i >= 0; i--) {
              heapify(arr, i);
          }
      }
      
      function heapify(arr, i) {     // 堆调整
          var left = 2 * i + 1,
              right = 2 * i + 2,
              largest = i;
      
          if (left < len && arr[left] > arr[largest]) {
              largest = left;
          }
      
          if (right < len && arr[right] > arr[largest]) {
              largest = right;
          }
      
          if (largest != i) {
              swap(arr, i, largest);
              heapify(arr, largest);
          }
      }
      
      function swap(arr, i, j) {
          var temp = arr[i];
          arr[i] = arr[j];
          arr[j] = temp;
      }
      
      function heapSort(arr) {
          buildMaxHeap(arr);
      
          for (var i = arr.length - 1; i > 0; i--) {
              swap(arr, 0, i);
              len--;
              heapify(arr, 0);
          }
          return arr;
      }
      var s=performance.now();

      heapSort(l5);

      var e=performance.now();
      this.time5=e-s;
      console.log(e-s);
      console.log(l5);
      this.result5=l5.join(" ");
      this.s5=1;//状态
    },
    xepx(){
      var len=this.ip1.length;
      var l6=Array.from(this.ip1);
      l6 = l6.map(Number);
      function shellSort(arr) {
      var len = arr.length;
      for (var gap = Math.floor(len / 2); gap > 0; gap = Math.floor(gap / 2)) {
          // 注意：这里和动图演示的不一样，动图是分组执行，实际操作是多个分组交替执行
          for (var i = gap; i < len; i++) {
              var j = i;
              var current = arr[i];
              while (j - gap >= 0 && current < arr[j - gap]) {
                  arr[j] = arr[j - gap];
                  j = j - gap;
              }
              arr[j] = current;
          }
      }
      return arr;
      }
      var s=performance.now();

      shellSort(l6);

      var e=performance.now();
      this.time6=e-s;
      console.log(e-s);
      console.log(l6);
      this.result6=l6.join(" ");
      this.s6=1;//状态
    }
  }
}
</script>

