<template>
    <div>
        <p></p>
    </div>
</template>

<script>
  export default {
    data() {
      const item = {
        date: '2016-05-02',
        name: '王小虎',
        address: '上海市普陀区金沙江路 1518 弄'
      };
      return {
        tableData: Array(20).fill(item)
      }
    },
    methods: {
        crpx(){
        var len=this.ip1.length;
        var l3=Array.from(this.ip1);
        l3 = l3.map(Number);
        var s=performance.now();

        let preIndex, current;
        for (let i = 1; i < len; i++) {
          preIndex = i - 1;
          current = l3[i];
          while (preIndex >= 0 && current < l3[preIndex]) {
            l3[preIndex + 1] = l3[preIndex];
            preIndex--;
          }
          l3[preIndex + 1] = current;
        }
    },
    },
  };
</script>

