<template>
    <div>
   <span><h2>选择排序算法演示</h2>
     <el-popover
    placement="bottom"
    width="300"
    trigger="click">
    <el-input
      placeholder="最小值"
      prefix-icon="el-icon-bottom"
      v-model="min">
    </el-input>
    <el-input
    placeholder="最大值"
    suffix-icon="el-icon-top"
    v-model="max" width="100">
    </el-input>
    <el-input
      placeholder="生成数量"
      v-model="count">
    </el-input> 
    <el-button  class="rnb" @click="randnumber"  type="success" size="small" plain>一键生成</el-button>
    <el-button slot="reference" class="randb"  type="primary"  plain>随机数</el-button>
    </el-popover>
   </span>
        <el-input
          placeholder="请输入排序内容"
          v-model="input1"
          clearable
          class="sr">
        </el-input>
        <div class="but"> 
        <el-button type="primary" @click="demo" plain v-if='index==0'>演示</el-button>
        <el-button type="primary" @click="pause" plain v-if='index==1'>暂停</el-button>
        <el-button type="primary" @click="demo" plain v-if='index==2'>继续演示</el-button>
        <el-button type="primary" @click="last" plain>上一步</el-button>
        <el-button type="primary" @click="next" plain>下一步</el-button>
        </div>
        <div id='xzxp' class="pxq">

        </div>
    </div>
</template>

<script>
export default {
  data() {
    return {
        input1:'',
        it0:[],
        itbb:[],
        demoes:[],
        index:0,
        finish:1,
        jq:0,jb:0,
        min:0,max:30,count:12,
    }
  },
  methods: {
    initiative(){
      this.it0=this.input1.split(" ");
      this.it0=this.it0.map(Number);
      var hasking='';
      var anima = document.getElementById('xzxp');
      this.it0.forEach((e) => {
        hasking += '<div class="num1" id="' + e + '" style="height:'+ e*20 +'px;left:0px">' + e + '</div>';
      });
      anima.innerHTML=hasking;
    },
    xzpx(it1){
      var temp;
      var time=0;
      this.index=1;
      for (var i = 0; i < it1.length; i++) {
        for (var j = i+1 ; j < it1.length; j++) {
          if (it1[j] < it1[i]) {
            temp = it1[i];
            it1[i] = it1[j];
            it1[j] = temp;
            this.jq++;
            let her=Array.from(it1);
            this.itbb.push(her);
            (() =>{
                let it = Array.from(it1);
                this.demoes.push(setTimeout(() => {
                  it.forEach((e, i) => {
                      let ji=i+1;
                      document.getElementById(e).style.left =(ji * 50+399) + 'px';
                  });
                  this.jb++;
                  if(this.jb==this.jq)
                  setTimeout(() => {
                       {
                          this.$message({
                          message: '排序完成',
                          type: 'success',
                          offset:100,
                          });
                          this.index=0;
                          this.finish=1;
                        }
                    }, 10);
                  // console.log(jb);
                  // console.log(jq);
                  console.log(it);
                }, time * 800));  
            })();
            time++;
          }
        }

      };
      return it1;
    },
    demo(){
      if(this.finish==1)
      {
        var ani = document.getElementById('xzxp');
        ani.innerHTML='';
        this.finish=0;
        this.jq=this.jb=0;
        this.it0=[];
        this.demoes=[];
        this.initiative();
        this.xzpx(this.it0);
      }
      else
      {
        var timi=0;
        for(var w=this.jb;w<this.jq;w++)
        {
          (() =>{
           let it = this.itbb[w];
           this.demoes.push(setTimeout(() => {
              it.forEach((e,i) => {
                  let ji=i+1;
                  document.getElementById(e).style.left = (ji * 50+399) + 'px';
              });
              this.jb++;
              if(this.jb==this.jq)
              setTimeout(() => {
                {
                  this.$message({
                  message: '排序完成',
                  type: 'success',
                  offset:100,
                  });
                  this.index=0;
                  this.finish=1;
                }
              }, 10);
              console.log(it);
            },timi*800)
           )
          })();
          timi++;
        };
        this.index=1;
      }
    },
    pause(){
      this.index=2;
      // console.log(this.demoes);
      let len=this.demoes.length;
      for(var i=0;i<len;i++)
      {
        clearTimeout(this.demoes[i]);
      }
      this.demoes=[];
    },
    next(){
      // console.log(this.itbb);
      if(this.jb<this.jq){
      this.itbb[this.jb].forEach
      ((e, i) => {
            let ji=i+1;
            document.getElementById(e).style.left= (ji * 50+399) + 'px';
      });
      this.jb++;
      }
      else{
        this.$message({
        message: '排序完成',
        type: 'success',
        offset:100,
        });
        this.index=0;
        this.finish=1;
      }
    },
    last(){
      this.jb--;
      this.itbb[this.jb].forEach
      ((e, i) => {
            let ji=i+1;
            document.getElementById(e).style.left= (ji * 50+399) + 'px';
      });
    },
    randnumber(){
      var l=[];
      function randomNum(minNum,maxNum){ 
          switch(arguments.length){ 
              case 1: 
                  return parseInt(Math.random()*minNum+1,10); 
              break; 
              case 2: 
                  return parseInt(Math.random()*(maxNum-minNum+1)+minNum,10); 
              break; 
              default: 
                  return 0; 
              break; 
          } 
      };
      for(var i=0;i<this.count;i++)
      {
        var doubt=randomNum(this.min,this.max);
         if(l.indexOf(doubt)==-1){//去重
              l.push(doubt);
          }
          else
          i--;
      }
      console.log(l);
      this.input1=l.join(" ");
    },
  }
}
</script>
 
<style scoped>
.el-popover
.el-input{
  margin-bottom: 8px;
}
.randb{
  position: absolute;
  right: 70px;
  top:150px
  /* float: right; */
}
.rnb{
  display: block;
  margin: 0px auto;
  margin-top: 10px;
}
.sr .el-input{
margin-top: 30px;
}

.but{
  text-align:center;
}
.but .el-button{
  margin-top: 30px;
  margin-bottom: 60px;
}
h2{
  text-align: center;
  color:#2c3e50;
  margin-bottom: 30px;
}
</style>
<style>
.pxq{
  position: relative;
  margin: 0 auto;
}
.num1{
  width:11px;
  margin: 10px;
  /* border-style: solid; */
  background: #f8c291;
  padding: 10px;
  position: absolute;
  transition: 1.0s;
  text-align: center;
}
</style>


